.main-layout {
  display: grid;
  grid-template-columns: 4rem 1fr 4rem;
  // margin-top: 15px;
}

.main-layout>* {
  grid-column: 2;
}


.narrow-layout {
  display: grid;
  grid-template-columns: 20rem 2fr 20rem;
  // margin-top: 15px;
}

.narrow-layout>* {
  grid-column: 2;
  // grid-row: 2;
}

.main-layout .full {
  grid-column: 1/-1;
}

.container {
  max-width: 1000px;
  padding: 1em;
  margin: auto;
}

footer {
  background-color: var(--gray3);
  color: var(--clr1bg);
  padding: 1em;
  text-align: center;
}


